@mixin buttonBackgroundNone() {
    background-color: transparent;
    border: none;
}

@mixin backgroundPosition($left, $top ){
    background-repeat: no-repeat;
    background-position: $left $top;
}

@mixin transition($prop, $duration, $tFunc) {
    transition: $prop, $duration, $tFunc;
    -webkit-transition: $prop $duration $tFunc;
    -moz-transition: $prop $duration $tFunc;
    -ms-transition: $prop $duration $tFunc;
    -o-transition: $prop $duration $tFunc;
}

@mixin filterForSvg() {
    filter: invert(25%) sepia(7%) saturate(1384%) hue-rotate(7deg) brightness(93%) contrast(88%);
    -webkit-filter: invert(25%) sepia(7%) saturate(1384%) hue-rotate(7deg) brightness(93%) contrast(88%);
}

@mixin borderRadius($topLeft, $topRight, $bottomLeft, $bottomRight ) {
    border-radius: $topLeft $topRight $bottomLeft $bottomRight;
    -webkit-border-radius: $topLeft $topRight $bottomLeft $bottomRight;
    -moz-border-radius: $topLeft $topRight $bottomLeft $bottomRight;
    -ms-border-radius: $topLeft $topRight $bottomLeft $bottomRight;
    -o-border-radius: $topLeft $topRight $bottomLeft $bottomRight;
}

$paddingForHeaderAndFooter:0 120px;
$activeColor:#bdae82;

*{
    box-sizing: border-box;
}

html{
    font-size: 20px;
    font-weight: 400;
    line-height: 40px;
    margin: 0;
    padding: 0;
    color: #fff;
}

body{
    height: 100vh;
}

.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    -webkit-clip-path: inset(100%);
            clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
}

.eco-container{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
}


.eco-header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 90px;
    width: 100%;
    padding: $paddingForHeaderAndFooter;
    background-color: #1c1c1c;
}

.eco-logo{
    @include buttonBackgroundNone();
    @include backgroundPosition(0, 0);
    @include transition(filter, 0.5s, easy-in);
    background-image: url("../assets/eco-sounds/svg/logo.svg");
    background-size: 50px 50px;
    width: 50px;
    height: 50px;
    cursor: pointer;
}

.eco-logo:hover{
    @include filterForSvg();
}

.eco-logo-active{
    @include filterForSvg();
}

.eco-menu__list{
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 40px;
    padding: 0;
    margin: 0;
    list-style: none;
}

.eco-menu__btn{
    @include buttonBackgroundNone();
    @include transition(color, .5s, easy-in);
    color:#fff;
    cursor: pointer;
}

.eco-menu__btn:hover{
    color: $activeColor
}

.eco-menu__btn-active{
    color: $activeColor;
}

.eco-main{
    @include backgroundPosition(0, 0);
    @include transition(opacity, .7s, easy-in);
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    background-color: #1c1c1c;
    background-image: url("../assets/eco-sounds/img/forest.jpg");
    opacity: 1;
}

.eco-main__text{
    position: absolute;
    top: 50px;
    font-size: 100px;
    line-height: 100px;
    text-align: center;
    opacity: 0;
    @include transition(opacity, 1s, easy-in);
}
.eco-main__main-icon{
    @include transition(filter, .5s, easy-in);
    @include transition(opacity, .5s, easy-in);
    @include backgroundPosition(0, 0);
    @include filterForSvg();
    background-size: 150px;
    background-image: url("../assets/eco-sounds/svg/play.svg");
    width: 150px;
    height: 150px;
    opacity: 1;
    cursor: pointer;
}
.eco-main__main-icon-pause{
    background-image: url("../assets/eco-sounds/svg/pause.svg");
}

.eco-main__main-icon:hover{
    filter: invert(1%);
    -webkit-filter: invert(1%);
}
.eco-main__main-icon-hide{
    opacity:0;
}

.eco-footer{
    height: 90px;
    width: 100%;
    background-color: #1c1c1c;
}   

.eco-footer__container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 100%;
    padding: $paddingForHeaderAndFooter;
}

.eco__github-container{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    width: 265px;
    gap: 20px;
}

.eco__github-link,
.eco-rsscholl{
    @include transition(color, 0.5s, easy-in);
    text-decoration: none;
    cursor: pointer;
    color: #fff;
}

.eco__github-link:hover,
.eco-rsscholl:hover{
    color:$activeColor ;
}

.eco__controls-container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 40px;
}

.eco__controls-btn{
    @include buttonBackgroundNone();
    @include backgroundPosition(0, 0);
    @include transition(filter, 0.5s, easy-in );
    width: 35px;
    height: 35px;
    background-size: 35px 35px;
    cursor: pointer;
}

.eco__controls-btn-active{
    @include filterForSvg();
}
.eco__controls-btn:hover{
    @include filterForSvg();
}

.eco__controls-btn-play{
    background-image: url("../assets/eco-sounds/svg/play.svg");
}

.eco__controls-btn-pause{
    background-image: url("../assets/eco-sounds/svg/pause.svg");
}

.eco__controls-btn-download{
    background-image: url("../assets/eco-sounds/svg/download.svg");
}

.eco__controls-volume{
    @include backgroundPosition(0, center);
    background-image: url("../assets/eco-sounds/svg/volume_up.svg");
    filter: invert(100%);
    -webkit-filter: invert(100%);
    background-size: 40px 40px;
    min-width: 40px;
    cursor: pointer;
}

.eco__controls-volume-off{
    background-image: url("../assets/eco-sounds/svg/volume_off.svg");
}

.eco__controls-volume:hover{
    filter: invert(75%) sepia(7%) saturate(1384%) hue-rotate(7deg) brightness(93%) contrast(88%);
    -webkit-filter: invert(75%) sepia(7%) saturate(1384%) hue-rotate(7deg) brightness(93%) contrast(88%);
}

.eco__controls-volume-range-container:hover,
.eco__controls-volume:hover + .eco__controls-volume-range-container{
    z-index: 2;
    opacity: 1;
}

.eco__controls-volume-container {  
    position: relative;
    display: flex; 
    justify-content: flex-start;
    align-items: center;
    width: 40px;
}

.eco__controls-volume-range-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    width: 100%;
    height: 160px;
    position: absolute;
    top:-150px;
    left: 0;
    z-index: -1;
    padding-bottom: 10px;
    padding-top: 10px;
    opacity: 0;
    @include transition(opacity, 0.5s, easy-in);
    cursor:row-resize;
}

.eco__controls-volume-range-up{
    height: 47%;
    width: 16px;
    background-color: rgba(255, 255, 255, 0.7);
    @include borderRadius(5%, 5%, 0, 0);
}

.eco__controls-volume-range-current{
    height: 6%;
    width: 20px;
    background-color: rgba(255, 255, 255, 0.9);
}

.eco__controls-volume-range-down{
    height: 47%;
    width: 16px;
    background-color: rgb(7, 37, 207);
    @include borderRadius(0, 0, 5%, 5%)
}

